<template>
<div class=''>
    <el-switch
        v-model="values"
        active-color="#13ce66"
        inactive-color="#ff4949">
    </el-switch>
</div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
    name:"ky-switch",
//import
components: {},
props:{
    value:{
        type:Boolean,
        default:true
    }
},
data() {
//
return {
    values:this.value

};
},
// data
computed: {},
//data
watch: {
    value:function(val){
        this.values=val
    }
},
//
methods: {

},
// - this
created() {

},
// - DOM
mounted() {

},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
